import React, { Component, Fragment } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { ListItem, ListItemText } from '@material-ui/core';
import packageUnitCodes from '../../../../jsonData/packageUnitCode.json';

class TotalPackageUnitCode extends Component {
  constructor(props) {
    super(props);
    this.state = {
      packageUnitCodeData: [],
      searchInput: '',
      selected: false,
      validators: ['required'],
      errorMessages: ['จำเป็นต้องกรอก'],
    };
    this.handleBlur = this.handleBlur.bind(this);
  }

  componentDidMount() {
    this.interval = null;
    this.setState({
      packageUnitCodeData: packageUnitCodes,
    });
  }

  componentWillUnmount() {
    this.setState({
      packageUnitCodeData: [],
    });
    clearTimeout(this.interval);
  }

  handleBlur(event) {
    event.preventDefault();
    this.interval = setTimeout(() => {
      this.setState(state => ({
        searchInput: state.selected ? state.searchInput : '',
      }));
    }, 200);
  }

  handleSeachInput = event => {
    event.preventDefault();
    this.setState({
      searchInput: event.target.value,
      selected: false,
    });
  };

  handleSelect = event => {
    event.preventDefault();
    this.setState({
      selected: true,
      searchInput: event.currentTarget.innerText.trim().slice(0, 2),
      validators: ['required', 'minStringLength: 2', 'maxStringLength: 2'],
      errorMessages: [
        'จำเป็นต้องกรอก',
        'ต้องกรอกให้ครบ 2 ตัวอักษร',
        'กรอกได้สูงสุด 2 ตัวอักษร',
      ],
    });
  };

  render() {
    const { handleSeachInput, handleSelect, handleBlur } = this;
    const {
      searchInput,
      validators,
      errorMessages,
      selected,
      packageUnitCodeData,
    } = this.state;

    const lowerCasedInput = searchInput.toLowerCase();

    let filteredPackageCodes = [];

    filteredPackageCodes =
      lowerCasedInput.length < 3
        ? packageUnitCodeData.filter(pkg =>
            pkg.code.toLowerCase().includes(lowerCasedInput)
          )
        : packageUnitCodeData.filter(pkg =>
            pkg.name.toLowerCase().includes(lowerCasedInput)
          );

    return (
      <Fragment>
        <TextValidator
          id="totalPackageUnitCode"
          name="totalPackageUnitCode"
          fullWidth
          required
          margin="normal"
          style={{ margin: 8 }}
          label="Total Package Unit Code"
          helperText="ลักษณะหีบห่อรวม"
          placeholder="กรอกรหัสหรือชื่อหีบห่อเพื่อค้นหา"
          type="search"
          onBlur={handleBlur}
          value={searchInput}
          validators={validators}
          errorMessages={errorMessages}
          onChange={handleSeachInput}
        />
        {selected || searchInput === '' ? (
          ''
        ) : (
          <div style={{ overflowY: 'auto', maxHeight: '280px' }}>
            {filteredPackageCodes.slice(0, 20).map(pkg => (
              <ListItem
                button
                key={pkg.code}
                onClick={handleSelect}
                value={pkg.code}
              >
                <ListItemText primary={pkg.code} secondary={pkg.name} />
              </ListItem>
            ))}
          </div>
        )}
      </Fragment>
    );
  }
}

export default TotalPackageUnitCode;
