import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CompanyBranchField = props => {
  const { handleChange, companyBranch } = props;
  return (
    <TextValidator
      name="companyBranch"
      id="companyBranch"
      required
      onChange={handleChange}
      value={companyBranch}
      label="สาขาที่อยู่ของบริษัทผู้นำเข้า"
      style={{ margin: 8 }}
      helperText="ไม่มีสาขาให้กรอก 0"
      fullWidth
      margin="normal"
      validators={['required', 'matchRegexp:^[0-9]*$', 'maxStringLength:6']}
      errorMessages={[
        'จำเป็นต้องกรอก',
        'กรอกได้เฉพาะตัวเลข 0-9',
        'ต้องกรอกไม่เกิน 6 หลัก',
      ]}
    />
  );
};

export default CompanyBranchField;
