import React, { Component, Fragment } from 'react';
import {
  FormControlLabel,
  FormControl,
  FormLabel,
  Radio,
  RadioGroup,
  Collapse,
} from '@material-ui/core';

class RadioControl extends Component {
  state = {
    value: '',
    checked: false,
  };

  handleChange = event => {
    const checkedValue = event.target.value;
    this.setState({ value: checkedValue });
    checkedValue === 'Y'
      ? this.setState({
          checked: true,
        })
      : this.setState({
          checked: false,
        });
  };

  render() {
    return (
      <Fragment>
        <FormControl
          fullWidth
          component="fieldset"
          style={{ margin: 8, marginTop: 25 }}
        >
          <FormLabel component="legend">{this.props.formLabel}</FormLabel>
          <RadioGroup
            name={this.props.radioName}
            value={this.state.value}
            onChange={this.handleChange}
          >
            <FormControlLabel
              value="Y"
              control={<Radio />}
              label={this.props.yesLabel}
            />
            <FormControlLabel
              value="N"
              control={<Radio />}
              label={this.props.noLabel}
            />
          </RadioGroup>
        </FormControl>

        <Collapse in={this.state.checked}>
          <Fragment>{this.props.children}</Fragment>
        </Collapse>
      </Fragment>
    );
  }
}

export default RadioControl;
