import React, { Fragment } from 'react';
import SelectValidator from 'react-material-ui-form-validator/lib/SelectValidator';
import { MenuItem } from '@material-ui/core';

const GuaranteeMethod = props => {
  const {
    handleChange,
    guaranteeMethod,
    guaranteeMethodData,
  } = props;
  return (
    <Fragment>
      {guaranteeMethodData.length === 0 ? (
        ''
      ) : (
        <SelectValidator
          id="guaranteeMethod"
          required
          name="guaranteeMethod"
          onChange={handleChange}
          value={guaranteeMethod}
          select
          label="Guarantee Method"
          helperText="วิธีการวางประกัน"
          style={{ margin: 8 }}
          fullWidth
          validators={['required']}
          errorMessages={['จำเป็นต้องเลือก']}
        >
          {guaranteeMethodData.map(option => (
            <MenuItem
              key={option.value}
              value={option.value}
            >
              {option.label}
            </MenuItem>
          ))}
        </SelectValidator>
      )}
    </Fragment>
  );
};

export default GuaranteeMethod;
