import React, { Fragment } from 'react';
import { MenuItem } from '@material-ui/core';

const TambonSelect = props => {
  const { selected, searchInput, handleSelect, filteredTambons } = props;

  return selected || searchInput === '' ? (
    ''
  ) : (
    <Fragment>
      {filteredTambons.slice(0, 5).map(tambon => (
        <MenuItem
          key={tambon.pid}
          onClick={handleSelect}
          value={tambon.name}
        >
          {tambon.name}
        </MenuItem>
      ))}
    </Fragment>
  );
};

export default TambonSelect;
